import { renderWithProviders } from "@rp/react/web/common/tools"
import MaintenancePage from "./MaintenancePage"

describe("Maintenance: ", () => {
  it("should render", async () => {
    const { getByText } = renderWithProviders(<MaintenancePage />)

    expect(getByText("We're under maintenance")).toBeInTheDocument()
    expect(
      getByText("Bigdata.com is currently undergoing scheduled maintenance."),
    ).toBeInTheDocument()
    expect(getByText("We will be back shortly. Thank you for your patience.")).toBeInTheDocument()
  })
})
